*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(46, 46, 46);
    --k:to left, #256BF4 40%, #3AC1DE;
}

body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.container-footer{
    background-image: linear-gradient(to left, #256BF4 , #3AC1DE);
    /* background-color: #15264a; */
    width: 100%;
    height: 290px;
    display: flex;
    justify-content: center;
    animation: gerak 20s ease infinite alternate;
}
@keyframes gerak {
    0%{
        background-size: 200%;
        background-position: left;
    }100%{
        background-size: 200%;
        background-position: right;
    }
    
}
.container-footer .wrapper-footer{
    width: 50%;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container-footer .wrapper-footer h1{
    color: white;

}
.container-footer .wrapper-footer h2{

    position:relative;
    bottom: 22px;
    color: white;
    margin-bottom: 20px;
}
.container-footer .wrapper-footer .icon{
    width: 360px;
    height: 70px;
    position: relative;
    bottom: 10px;
    /* margin-bottom: 20px; */
    /* background-color: aqua; */
    display: flex;
    justify-content: space-evenly;
}
.container-footer .wrapper-footer .icon .isi-icon{
    background-color: whitesmoke;
    width: 42px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    /* border: 1px solid black; */
}
.container-footer .wrapper-footer .icon .isi-icon i{
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-footer .wrapper-footer .icon .isi-icon:first-child i{
  color: rgb(204,40,109);

}
.container-footer .wrapper-footer .icon .isi-icon:hover:first-child{
   background-image: linear-gradient(45deg,rgb(255,176,79),rgb(236,36,65),rgb(169,60,151),rgb(169,60,151),rgb(86,85,163));
   box-shadow: 0px 0px 5px rgb(236,36,65);
   transform: scale(1.1);
   transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:first-child i{
    transform: scale(1.1);
    color: white;
    transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:nth-child(2) i{
    color: black;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(2) {
    background-color:black;
    transform: scale(1.1);
    box-shadow: 0px 0px 5px black;
    transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(2) i{
    background-color:black;
    color: white;
    transform: scale(1.1);
    transition: 0.5s;
}
.container-footer .wrapper-footer .icon .isi-icon:nth-child(3) i{
    color: #0e76a8;
    
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(3) {
    background-color: #0e76a8;
    box-shadow: 0px 0px 5px #0e76a8 ;
    transform: scale(1.1);
    transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(3) i{
    background-color: #0e76a8;
    transform: scale(1.1);
    color: white;
    transition: 0.5s;
}
.container-footer .wrapper-footer .icon .isi-icon:nth-child(4) i{
    color: #3b5998 ;

}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(4) {
    background-color: #3b5998 ;
    box-shadow: 0px 0px 5px #3b5998  ;
    transform: scale(1.1);
    transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(4) i{
    background-color: #3b5998 ;
    transform: scale(1.1);
    color: white;
    transition: 0.5s;
}
.container-footer .wrapper-footer .icon .isi-icon:nth-child(5) i{
    color:rgb(240,80,51);
 
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(5) {
    background-color: rgb(240,80,51);
    transform: scale(1.1);
    box-shadow: 0px 0px 5px rgb(240,80,51);  
    transition: 0.3s;
}
.container-footer .wrapper-footer .icon .isi-icon:hover:nth-child(5) i{
    color: white;
    transform: scale(1.1);
    transition: 0.3s;
}
.container-footer .wrapper-footer .copy{
    color: white;
    font-size: 15px;
    position: relative;
    top: 15px;
}
.container-footer .wrapper-footer .copy a{
    /* color: red; */
    text-decoration: none;
    color: white;
    font-size: 15px;
}
