*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(46, 46, 46);
    --k:to left, #256BF4 40%, #3AC1DE;
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.container-main1{
    
    height:480px;
    /* height:100vh; */
    margin-top: 140px;
    display: flex;
    justify-content: center;
}
.container-main1 .wrapper-main1{
    width: 75%;
    display: flex;
    /* background-color: blue; */
    align-items: center;
    justify-content: space-between;
}


.container-main1 .wrapper-main1 .profile-home{
    /* background-color: red; */
    width: calc(100%/2);
}
.container-main1 .wrapper-main1 .profile-home h1{
      -webkit-background-clip: text;
    background-clip: text; 
    background-image: linear-gradient(to left, #256BF4 40%, #3AC1DE);
    color: transparent;
    font-size: 40px;
}
.runingtext{
    margin-left: 2px;
    position: relative;
    bottom:21px;
    display: flex;
    font-size: 25px;
    font-weight: 700;
    width: 400px;
    align-items: center;
}

.animated-text{
          position: relative;
             left: 10px;
            overflow: hidden;
            white-space: nowrap;
            color: black;
            border-right: 1px solid  #256BF4 ;
            padding-right: 5px;
            animation: typing 2.5s steps(20),blink 2.5s ;
            animation-fill-mode: forwards;
           
        }
        @keyframes typing {
            0%{
                width: 0%;
            }100%{
                width: 314px;
               
            }
            
        }
        @keyframes blink {
            0%,10%{

                border-right: 1px solid black;
            }11%,20%{
                border-right: 1px solid white;
            }21%,30%{
                border-right: 1px solid black;
            }
            31%,40%{
                border-right: 1px solid white;

            }41%,50%{
                border-right: 1px solid black;

            }51%,60%{
                border-right: 1px solid white;

            }61%,70%{
                border-right: 1px solid black;

        }71%,80%{
                border-right: 1px solid white;

            }81%,98%{
                border-right: 1px solid black;

            }99%,100%{
                border-right: 1px solid white;

            }
            
        }
.wrapper-main1 .profile-home .garis{
    width: 93%;
    height: 1px;
    background-color: rgb(100, 100, 100);
}
.wrapper-main1 .profile-home p{
    margin-top: 25px;
    color: var(--font);
    font-weight: 500;
    /* background-color: red; */
    width: 94%;
}
.wrapper-main1 .profile-home .cv{
    background-color: white;
    border: none;
    background: linear-gradient(90.55deg, #256BF4 -13.39%, #3AC1DE 99.74%);
    padding: 11px 43px;
    margin-top: 10px;
    border-radius: 25px;
}
.wrapper-main1 .profile-home .cv:hover{
    box-shadow: 0px 5px 10px rgba(0, 118, 255, 0.8);
    transform: scale(1.05);
}
.wrapper-main1 .profile-home .cv a{
    
    text-decoration: none;
    color:  white;
    font-weight: 600;
    font-size: 16px;
}


.container-main1 .wrapper-main1 .img-home {
    position: relative;
    display: flex;
    justify-content: end;
    align-items: center;
    width: calc(100%/2);
    /* background-color: red; */
}

.container-main1 .wrapper-main1 .img-home  img{
    width: 430px;
    filter: drop-shadow(0px 3px 5px rgb(212, 212, 212));
    position: relative;
    left: 140px;
}