*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(51, 50, 50);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.container-main3{
    /* background-color: yellow; */
    margin-top: 30px;
    width: 100%;
    height: 700px   ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.container-main3 .judul h1{
    background-color: white;
    text-align: center;
    font-size: 35px;
    font-weight: 700;
    color: black;
}
.container-main3 .wrapper-main3{
    width: 75%;
    margin-top: 20px;
    height: 490px;
    display: flex;
    
}
.container-main3 .wrapper-main3 .content-main3{
    display: flex;
    flex-direction: column      ;
    justify-content: center;
    align-items: center;
    /* border: 1px solid black; */
}
.container-main3 .wrapper-main3 .content1-main3{
    width: 27%;
}
.container-main3 .wrapper-main3  .content2-main3{
    /* background-color: ; */
    width: 73%;
}
.container-main3 .wrapper-main3 .content1-main3 p{
    font-weight: 600;
    color: var(--font);
    position: relative;
    bottom: 20px;
    left: 1px;
    font-size: 17px;
}
.container-main3 .wrapper-main3 .content1-main3 .education{
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    background-image: linear-gradient(-80deg, #256BF4, #3AC1DE );

}
/* =================================== */
.container-main3 .wrapper-main3 .content2-main3 .isi-2{
    border-radius: 10px;
    box-shadow: 2px 10px 10px rgb(213, 210, 210),-2px -1px 5px rgb(215, 211, 211);
    width:100%;
    height: 440px;
    display: flex;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok{
    height: 100%;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok1{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 45%;

}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok2{
    width: 55%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;

}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .btn{
    background-image: linear-gradient(-10deg, #256BF4 , #3AC1DE);
    width: max-content;
    padding: 6px 10px;
    border-radius: 4px;
    color: white;
    font-size: 15px;
    font-weight: 600  ;
    position: relative;
    /* left: 6.5%; */
    top: 23px;

}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .btn1{
    position: relative;
    left: 23px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .btn2{
    position: relative;
    left: 22px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg{
    /* background-color:    aqua; */
    display: flex;
    position: relative;
    width: 370px;
    top: 50px;
    left: 30px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .diagram{
    border-radius: 5px;
    background-image: linear-gradient(to right, #256BF4 , #3AC1DE);
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .diagram1{
    width: 4px;
    height: 250px;
    z-index: 1;
    
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .diagram2{
    width: 4px;
    height: 340px;
    position: relative;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .diagram .bulet-diagram{
    width:15px;
    height:15px ;
    background-image: linear-gradient(to right, #256BF4 , #3AC1DE);
    border-radius: 50%;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .diagram .buletdg-2{
    margin-top: 70px;
}
/* -------------------- */
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg{
    /* background-color: red; */
    width: 400px;
    height: 360px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text{
    /* background-color: red; */
    height: 65px;
    position: relative;
    bottom: 31px;
    left: 15px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text2{
    position: relative;
    bottom: 34px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text3{
    position: relative;
    bottom: 38px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text4{
    position: relative;
    bottom: 42px;
}
/* ======== */
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text h4{
    font-size:18px ;
    font-weight: 600;
    color: var(--font);
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text p{
    position: relative;
    color: var(--font);
    bottom: 25px;
    font-weight: 500;
    font-size: 14px;
}
.container-main3 .wrapper-main3 .content2-main3 .isi-2 .blok .kotak-dg .ketik-dg .isi-text .p2{
    position: relative;
    bottom: 41px;
}