*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(51, 50, 50);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

.container-main5{

    width: 100%;
    height: 420px;
    display: flex;
    justify-content: center;
    margin-top: -30px;
}
.container-main5 .wrapper-main5{
    width: 75%;
    height: 400px;
}
.wrapper-main5 .contact h1{
    font-weight: 700;
    text-align: center;
    font-size: 35px;
    margin-bottom: 40px;
}
.wrapper-main5 .judul-main5 h1{
    -webkit-background-clip: text;
    font-size: 30px;
    color: transparent;
    background-clip: text; 
    background-image: linear-gradient(to left, #256BF4 50%, #3AC1DE);
}
.wrapper-main5 .judul-main5 p{
    font-weight: 500;
    font-size: 16px;
    margin-top: -12px;
    color: var(--font);
}
/* ============/ */
.wrapper-main5 .content-main5{
    width: 100%;
    height: 240px;
    display: flex;
    justify-content: space-between;
}
.wrapper-main5 .content-main5 .kotak{
    margin-top: 40px;
    width: 270px;
    border-radius: 5px;
    height: 77px;
    box-shadow: 0px 0px 7px rgb(169, 168, 168);
    border-bottom:2px solid #3AC1DE;
    display: flex;
}
.wrapper-main5 .content-main5 .kotak .img{
    width: 28%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper-main5 .content-main5 .kotak .img img{
    width: 40px;
}
.wrapper-main5 .content-main5 .kotak .isi {
    margin-left: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.wrapper-main5 .content-main5 .kotak .isi .nama-icon{
    color: var(--font);
    font-weight: 600;
    margin-top: 8px;
}
.wrapper-main5 .content-main5 .kotak .isi .keterangan{
    margin-top: -15px;
    color: rgb(101, 100, 100);
    font-size: 12px;
}
.wrapper-main5 .content-main5 .kotak .isi .hubungi{
    margin-top: -10px;
    font-weight: 600;
    -webkit-background-clip: text;
    background-clip: text; 
    background-image: linear-gradient(-45deg, #256BF4 35%, #3AC1DE);
    color: transparent;
    cursor: pointer;
    width: max-content;
    font-size: 13px;
}
.wrapper-main5 .content-main5 .kotak:hover{
    transition: 0.3s;
    /* background-color: black; */
    border-bottom:2px solid #3AC1DE;
    box-shadow: 0px 0px 7px #256BF4;
}

.wrapper-main5 .content-main5 .kotak:hover .hubungi{
    animation: gerak 3s ease infinite alternate;
}
@keyframes gerak {
    0%{
        background-size: 250%;
        background-position: left;
    }100%{
        background-size: 250%;
        background-position: right;
    }
    
}