*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(51, 50, 50);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

.container-main4{
    /* background-color: aqua; */
    margin-top: 70px;
    width: 100%;
    display: flex;
    justify-content: center;
    height: 1300px;
}
.container-main4 .wrapper-main4{
    width: 75%;
    height: 400px;
  
}
.wrapper-main4 h1{
    font-size: 35px;
    background-color: white;

    font-weight: 700;

    margin-bottom: 100px;
    text-align: center;
}
.wrapper-main4 .content-main4{
    margin-top: -30px;  
    width: 100%;
    height: 600px;
}
.wrapper-main4 .judul-content{

    height: 90px;

}
.wrapper-main4 .judul-content h2{
    -webkit-background-clip: text;
    font-size: 30px;
    background-clip: text; 
    background-image: linear-gradient(to left, #256BF4 50%, #3AC1DE);
    color: transparent;

}
.wrapper-main4  .judul-content p{
    position: relative;
    font-weight: 500;
    font-size: 16px;
    bottom: 17px;
    color: var(--font);
} 
.wrapper-main4  .judul-content .p2{
    bottom: 32px;
}
.wrapper-main4 .isi-content{
   
   
    width: 100%;
    /* margin-top: 50px; */

}
.wrapper-main4 .isi-content .blok{
    
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.wrapper-main4 .isi-content .blok .square{
    margin-top: 50px;
    background-color: white;
    border-radius: 7px;
    width: 310px;
    height: 390px;
    box-shadow: 0px 0px 5px rgb(156, 156, 156);
    display: flex;
    justify-content: center;
    padding: 3px 3px 10px 3px;
}
.wrapper-main4 .isi-content .blok2 .square{
    background-color: white;
    border-radius: 7px;
    margin-top: 5px;
    width: 310px;
    height: 390px;
    box-shadow: 0px 0px 5px rgb(156, 156, 156);
    display: flex;
    justify-content: center;
    padding: 3px 3px 10px 3px;
}
.wrapper-main4 .isi-content .blok .square:hover{
    box-shadow: 0px 0px 20px #103683;
    transition: 0.5s;
}
.wrapper-main4 .isi-content .blok .square .isi-square{
    margin-top: 15px;
    width: 280px;
    height: 349px;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-square{
    border-radius: 7px;
    width: 100%;
    height: 195px;
    background-color: rgb(32, 32, 32);
    
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s1{
    background-image: url(images\ \(1\).jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s2{
    background-image: url(Aplikasi-Belajar-Online-yang-Lengkap-Praktis-dan-Asyik.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s3{
    background-image: url(WhatsApp-about.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s4{
    background-image: url(psychology-web-design-00-lead.jpg  );
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s5{
    background-image: url(whatsapp-image-2021-07-22-at-20-46-08-60f977217aa978226e73fc92.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.wrapper-main4 .isi-content .blok .square .isi-square .img-s6{
    background-image: url(rrr.png);
    background-size: cover;
    background-position: right;
    background-repeat: no-repeat;
}


/* ======================== */
.wrapper-main4 .isi-content .blok .square .isi-square .judul-project{
    margin-top: 13px;
    font-weight: 600;   
    font-size: 18px;
}

/* ======================== */
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon{
    width: 100%;
    /* height: 50px; */
    display: flex;
    margin-top: 5px;
}

.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(226, 232, 244);
    width: fit-content;
    height: 20px;
   
    /* padding: 3px 5px; */
    /* border-radius: 5px; */
    margin-top: 4px;

}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon1{
    width: 58px;
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon2{
    width: 53px;
    margin-left: 10px;
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon3{
    width: 93px;

}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon .text-ic{
    /* background-color: aqua; */
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    /* height: 29px; */
    position: relative;
    right: 5px;
    top: 2px;
}

.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon i{
    font-size: 14px;
   color: var(--font);
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon:first-child i{
   color: rgb(221,75,37);
   
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon:nth-child(2) i{
   color: rgb(46,92,162);
   
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon:nth-child(3) i{
   color: rgb(246,234,58);
   background-color: black;
   width: fit-content;
   height: 15.6px;
   position: relative;
   bottom: 3px;
   
}
.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon:nth-child(3) i .g{
   color: yellow;
   background-color: rgb(234, 231, 231);
   position: absolute;
   border: none;
   top: 0;
   width: 100%;
   /* width: fit-content; */
   height: 1.2px;
}

.wrapper-main4 .isi-content .blok .square .isi-square .box-icon .icon p{
    position: relative;
    left: 5px;
    bottom: 2px;
    /* margin-left: 5px; */
    /* margin-bottom: 15px; */
    font-size: 10px;
    color: var(--font);
    font-weight: 500;
}
/* =========== */
.wrapper-main4 .isi-content .blok .square .isi-square p{
    /* margin-bottom: 40px; */
    position: relative;
    bottom: 5px;
    color: var(--font);
    font-size: 13px;
    font-weight: 400;
    /* color: red; */
}
.wrapper-main4 .isi-content .blok .square .btn-project{
    /* background-color: red; */
    position: relative;
    top: 0px;
}
.wrapper-main4 .isi-content .blok .square .btn-project button{
    width: min-content;
    height: fit-content;
    border: none;
    border-radius: 5px;
    

}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button a{
    text-decoration: none;
    color: white;
    font-weight: 500;
    font-size: 14px;
    
}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button:nth-child(1){
    background-image: linear-gradient(-45deg, #256BF4 , #3AC1DE);
    padding: 2px 5px;
    box-shadow: 0px 0px 1px #256BF4;
}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button:nth-child(2){
    padding: 1px 4px;
    margin-left: 6px;
    background-color: white;
    border: 2px solid #3AC1DE;
    color: red;
}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button:nth-child(2) a{
    -webkit-background-clip: text;
    background-clip: text; 
    background-image: linear-gradient(to right, #256BF4 , #3AC1DE);
    color: transparent;
}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button:hover{
    box-shadow: 0px 0px 10px #256BF4;
}
.wrapper-main4 .isi-content .blok .square .isi-square .btn-project button:nth-child(2):hover{
    box-shadow: 0px 0px 10px #3AC1DE;
}