@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Caveat:wght@400;500;600;700&family=Coming+Soon&family=Heebo:wght@300;400;500;600;700;800&family=Lilita+One&family=Maven+Pro:wght@400;500;600;700;800;900&family=Montserrat&family=Outfit:wght@300;400;500;600&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,800&family=REM:wght@400;500;600&family=Silkscreen&display=swap');
*{
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
  
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    overflow-X:hidden ;
}
.container{
    width: 100%;
    display: flex;
    justify-content: center;
    
}
.navbar{
    width: 100%;
    background-color: red;
    box-shadow: 0px 2px 2px rgb(191, 190, 190);

}
.container .wrapper{
    position: fixed;
    background-color: rgba(255, 255, 255,0.5);
    /* background-color: red; */
    /* width: ; */
    z-index: 5;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.container .wrapper h1{
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to top, #256BF4 3%, #3AC1DE);
    font-size: 30px;
    color: transparent;
}


.container .wrapper ul{
    list-style: none;
    display: flex;
}
.container .wrapper ul li{
    padding: 0px 20px;
}
.container .wrapper ul li a{
    font-weight: 600;
    text-decoration: none;
    position:relative;
    color: black;
}

.container .wrapper ul li a:hover{
    color: var(--blue);
    border-bottom: 2px solid #3AC1DE ;

}
