*{scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(51, 50, 50);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.container-main2{
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-main2 .wrapper-main2{
    width: 75%;

}
.container-main2 .wrapper-main2 .judul-about{
    text-align: center;

    /* background-color: red; */
    color: black;
    position: relative;
    font-size: 35px;
    z-index: 1;
}

.container-main2 .wrapper-main2 .isi-main2{

    width: 100%;
    display: flex;
    height: 550px;
    margin-top: -10px;
}
.container-main2 .wrapper-main2 .isi-main2 .img-main2{

    width: 50%;
    height: 100%;
    display: flex;
    justify-content:start;
    position: relative;
    left: 5px;
    align-items: center;
}
.container-main2 .wrapper-main2 .isi-main2 .img-main2 .belakang{
    left: 40px;
  
    position: relative;
    width: 300px;
    height: 397px;
    transform: rotate(-1deg);
    background-color:#315fbb ;
    border-radius: 20px;
}
.container-main2 .wrapper-main2 .isi-main2 .img-main2 .depan{
    background-color: #7799dd;
    left:  42px;
    position: absolute;
    width: 300px;
    height: 430px;
    transform: rotate(7deg);
    border-radius: 20px;
    display: flex;
    align-items:end;
    /* justify-content: center; */
}
.container-main2 .wrapper-main2 .isi-main2 .img-main2 .depan img{
    width: 240px;
    position: relative;
    top: 10px;
    /* bottom: 60px; */
    left: 70px;
}
/* - */
.container-main2 .wrapper-main2 .isi-main2 .aboutme{
    position: relative;
    top: 10px;
    left: 20px;
    width: 50%;
    height: 98%;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .text-about{

    height: 250px;
    position: relative;

    display: flex;
    flex-direction: column;
    padding-left: 60px;
    justify-content: center;
    align-items: start;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .text-about h1{
    background-image:  linear-gradient(to left, #256BF4 10%, #3AC1DE);
    -webkit-background-clip: text;
    background-clip: text;
    font-size: 25px;
    color: transparent;
    font-weight: 700;
    text-align: left;
    
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .text-about .h1dua{
    position: relative;
    text-align: left;
    bottom: 30px;
    /* margin-bottom: 80px; */
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .text-about p{
    font-weight: 500;
    font-size: 16px;
    color: var(--font);
    /* background-color: aqua; */
    width:85% ;
    /* text-align: center; */
    position: relative;
    bottom: 50px;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .text-about h1 span{
    color: var(--font);
    
}

/* =========garis */
.container-main2 .wrapper-main2 .isi-main2 .aboutme .garis-about{
    background-color: rgb(97, 94, 94);
    width: 78%;
    height: 2px;
    position: relative;
    bottom: 25px;
    left: 0%;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .garis-about .strip{
    background-color: var(--font);
    position: relative;
    top: 1px;
    width: 133px;
    margin: 0px 5px;
    height: 2px;
}

/* --------------------- */
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about{
    position: relative;
    bottom: 10px;
    width: 79%;
    display: flex;
    justify-content: space-between;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak{
    cursor: pointer;
    border-radius: 8px;
    box-shadow: 0px 2px 3px var(--blue) ;
    border:1px solid var(--blue);
    width: 135px;
    height: 95px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak h3{
    font-size: 20px;
    color: var(--blue);
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak .h3-1{
    position: relative;
    top: 6px;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak .h31{
    position: relative;
    top: 30px;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak p{
    font-size: 14px;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak .p1-1{
    position: relative;
    bottom: 10px;
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak .p1{
    position: relative;
}

.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak .p2{

    position: relative;
    bottom: 30px;
}

/* - */
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak:hover{
    box-shadow: 0px 4px 6px var(--blue), 0px -1px 6px var(--blue); 
    transform: scale(1.1);
    background-image: linear-gradient(45deg, #256BF4 10%, #3AC1DE);
}
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak:hover p ,
.container-main2 .wrapper-main2 .isi-main2 .aboutme .kotak-about .kotak:hover h3{
    color: white;
}