*{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root{
    --bg:white;
    --blue:#3AC1DE  ;
    --font:rgb(51, 50, 50);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.container-main22{
    margin-top: 60px;
    /* background-color: red; */
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
}
.container-main22 .wrapper-main22{

    width: 75%;
    height: 600px;
}
.container-main22 .wrapper-main22 h1{
    font-size: 35px;
    color: black;
    background-color: white;
    text-align: center;
}
.wrapper-main22 .isi-content{
    width: 100%;
    height: 500px;
    display: flex;
}
.wrapper-main22 .isi-content .isi1{
    width: 65%;
    height: 100%;
}
.wrapper-main22 .isi-content .isi2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
    width: 35%;
    height: 100%;
}
.wrapper-main22 .isi-content .isi2 .jd{
    display: flex;
    flex-direction: column;
    align-items: end;
    height: 110px;
}
.wrapper-main22 .isi-content .isi2 .jd2{
    display: flex;
    flex-direction: column;
    align-items: end;
    height: 90px;
}
.wrapper-main22 .isi-content .isi2 h1,.wrapper-main22 .isi-content .isi2 p{
    margin-right: 90px;
    color: var(--font);
    
}
.wrapper-main22 .isi-content .isi2 p{
    font-size: 17px;
    font-weight: 600;
    position: relative;
    bottom: 10px;
}
.wrapper-main22 .isi-content .isi2 h1{
    position: relative;
    bottom: 10px;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    background-image: linear-gradient(-80deg, #256BF4, #3AC1DE );
   
}
.wrapper-main22 .isi-content .isi2 .h12{
    position: relative;
    bottom: 58px
}
.wrapper-main22 .isi-content .isi2 .p2{
    position: relative;
    bottom: 39px;

}


/* ================= */
.wrapper-main22 .isi-content .isi1{
    /* background-color: red; */
    display: flex;
    align-items: center;
    /* justify-content: center; */
}
.wrapper-main22 .isi-content .isi1 .content-isi1{
    /* background-color: aqua;   */
    margin-left: 50px;

    width: 85%;
    height: 80%;
    padding: 2px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgb(168, 166, 166);
    
}

.wrapper-main22 .isi-content .isi1 .content-isi1 .line{
  

    width: 100%;
    height: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line1{
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;

}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line2{
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;

}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .proper{
    box-shadow: 0px 0px 10px rgb(168, 166, 166);
    background-color: rgb(201, 194, 194);
    position: absolute;
    bottom: -1px;
    width: 90%;
    height: 1px;
}

.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak{

    width: 170px;
    height: 160px;
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1{
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bulet{
    width: 73px;
    height: 73px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow:-1px 1px 5px rgb(59, 58, 58);
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bulet img{
    width: 60px;

   
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-1 img,
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-2 img{
    width: 65px;
    position: relative;
    top: 1px;

}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-3 img,
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-4 img{
    width: 48px;

}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-5 img,
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec1 .bul-6 img{
    width: 58px;
}
/* ----------- */
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec2{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    color: var(--font);
    align-items: center;
    height: 50px;
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .sec2 .nama{
    position: relative;
    bottom: 4px;
    font-size: 16px;
    font-weight: 600;
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .re{
    font-size: 16px;
    font-weight: 400;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    bottom: 20px;
    height: 55px;
  
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .re p:first-child{
    font-size: 15px;
    position: relative;
    bottom: 16px;
}
.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak .re .dua{
    font-size: 15px;
    position: relative;
    bottom: 50px;
   
}

.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak:hover .sec1 .bulet{
    box-shadow:0px 0px 8px rgb(59, 58, 58);
   
    transform: scale(1.1);
    transition: 0.3s;
}

.wrapper-main22 .isi-content .isi1 .content-isi1 .line .kotak:hover .sec1 .bulet img{
    transition: 0.3s;
    transform: scale(1.1);
}
